@import url(https://fonts.googleapis.com/css2?family=Recursive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100vh;
  width: 100vw;
  margin: 0;
  background-color: #553d67;
  font-family: "Recursive", sans-serif;
  color: #f64c72;
}

.App {
  height: 600px;
  width: 80%;
  background-color: #242582;
  margin: auto auto;
  border-radius: 5%;
}

.mainBody {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}

h1 {
  text-align: center;
}

input {
  border: none;
  height: 42px;
  width: 30%;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 17px;
  border-radius: 5%;
}

input:focus {
  outline: none;
}

button {
  height: auto;
  width: auto;
  background-color: transparent;
  border: none;
  border-radius: 20%;
  font-size: 18px;
  font-weight: 700;
  color: #f64c72;
  cursor: pointer;
}

button:hover {
  text-shadow: 2px 2px #888;
}

button:focus {
  outline: none;
}

.variableInput {
  padding-left: 8px;
  height: 100%;
  width: 50%;
  border-left: 3px solid #99738e;
}

.bmiTitle {
  text-align: center;
  color: #f64c72;
  margin-bottom: 10%;
}

.bmiDisplay {
  margin-top: 70px;
  font-size: 28px;
}

.purple {
  color: purple;
}
.amber {
  color: rgb(255, 145, 0);
}
.green {
  color: green;
}
.red {
  color: red;
}
.pink {
  color: #f64c72;
}

.variableConverter {
  margin-left: 5px;
  height: 100%;
  width: 50%;
}

.converterTitle {
  text-align: center;
  color: #f64c72;
  margin-bottom: 10%;
}

.conversionDisplay {
  margin-top: 50px;
  font-size: 20px;
}

.key {
    position: absolute;
    display: flex;
    flex-direction: column;
    margin-left: 90%;
    margin-bottom: 10%;
}
.purpleBlock {
    background-color: purple;
    height: 10px;
    width: 10px;
}
.amberBlock {
    background-color: rgb(255, 145, 0);
    height: 10px;
    width: 10px;
}

.greenBlock {
    background-color: green;
    height: 10px;
    width: 10px;
}

.redBlock {
    background-color: red;
    height: 10px;
    width: 10px;
}


